export default {
	data() {
		return {
			title:"",
			strings:""
		}
	},
	onLoad:function(e){
		uni.request({
			url: 'https://unidemo.dcloud.net.cn/api/news/36kr/' + e.newsid,
			method: 'GET',
			data: {},
			success: res => {
				console.log(res);
				this.title=res.data.title;
				this.strings=res.data.content;
			},
			fail: () => {},
			complete: () => {}
		});
	},
	methods: {
		
	}
}
